*{margin:0;padding:0;box-sizing:border-box;font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif}.mg-25{margin:25px 25px 25px 25px}.main-container{padding-top:50px;display:flex}.left-container{width:50%;align-items:center;background-color:#fff;display:flex;justify-content:center;align-items:center}.bigfont h1{margin:15px 0;font-size:45px}.bigfont h3{margin:15px 0}.bigfont span{color:#ff7704}.icon-pack{display:flex;margin:20px 0px;width:100%;height:50px;align-items:center;justify-content:flex-start}.icon-image{width:50px;height:50px;border-radius:25px;background-color:#030303;display:flex;justify-content:center;align-items:center;margin-right:25px}.icon-image :hover{transform:scale(1.2);transition:all .5s}.icon{width:32px;height:auto}.right-container{width:50%;height:max-content;background-color:#fff;display:flex;justify-content:center;align-items:center;min-height:300px}.round-img{width:300px;height:300px;border-radius:200px}.img-wraper{display:flex;align-items:center;justify-content:center;padding-top:75px}@media screen and (max-width: 767px){.main-container{flex-direction:column;height:max-content}.left-container{width:inherit;height:auto}.left-container h1{font-size:40px}.right-container{width:fit-content;width:inherit}}.button{border-radius:50px;background-color:#030303;border:none;color:#fff;text-align:center;font-size:20px;padding:10px;width:200px;transition:all .5s;cursor:pointer;margin:5px}.button span{cursor:pointer;display:inline-block;position:relative;transition:.5s}.button span:after{content:"â†“";position:absolute;opacity:0;top:0;right:-20px;transition:.5s}.button:hover span{padding-right:25px}.button:hover span:after{opacity:1;right:0}